import { Page, Section } from "admiral";
import { Space, Button, Descriptions, Flex, message, Tag, Modal } from "antd";
import { generatePath, Link, useNavigate } from "react-router";
import { Typography } from "antd";
import { useParams } from "react-router";

import { formatDate } from "@/utils/date-format";
import { useGetData } from "@/app/_hooks/use-get-data";
import { templateMessagingDetail } from "../_data";

export const Component = () => {
  const navigate = useNavigate();
  const params = useParams();
  const { data, loading } = useGetData(templateMessagingDetail(params.id));

  const breadcrumbs = [
    {
      label: "Master Data",
      path: "/master-data",
    },
    {
      label: "Template Messaging",
      path: "/master-data/template-messaging",
    },
    {
      label: `Template Details: ${data?.data?.nama_template || ""}`,
      path: "#",
    },
  ];

  const items = [
    {
      key: "nama_template",
      label: "Nama Template",
      children: <Typography.Text strong>{data?.data?.nama_template ?? "-"}</Typography.Text>,
    },
    {
      key: "category",
      label: "Category",
      children: (
        <Typography.Text strong>{data?.data?.category ?? "-"}</Typography.Text>
      ),
    },
    {
      key: "trigger",
      label: "Trigger",
      children: (
        <Typography.Text strong>{data?.data?.trigger ?? "-"}</Typography.Text>
      ),
    },
    {
      key: "status",
      label: "Status",
      children: (
        <Tag color={data?.data?.status === "Active" ? "green" : "red"} style={{ border: 'none' }}>
          {data?.data?.status ?? "-"}
        </Tag>
      ),
    },
    {
      key: "created_at",
      label: "Created At",
      children: (
        <Typography.Text strong>{formatDate(data?.data?.created_at) ?? "-"}</Typography.Text>
      ),
    },
    {
      key: "updated_at",
      label: "Updated At",
      children: (
        <Typography.Text strong>{formatDate(data?.data?.updated_at) ?? "-"}</Typography.Text>
      ),
    },
  ];

  return (
    <Page
      topActions={
        <Flex gap={10}>
          <Link
            to={generatePath("/master-data/template-messaging/:id/update", {
              id: params.id,
            })}
          >
            <Button htmlType="button" type="primary">
              Edit
            </Button>
          </Link>
        </Flex>
      }
      title={`Template Details: ${data?.data?.nama_template || ""}`}
      breadcrumbs={breadcrumbs}
      goBack={() => navigate("/master-data/template-messaging")}
      noStyle
    >
      <Section loading={loading}>
        <Space direction="vertical" size="middle" style={{ width: "100%" }}>
          <Section title="Template Information">
            <Descriptions
              bordered
              layout="horizontal"
              items={items}
              column={{
                md: 1,
                lg: 2,
                xl: 2,
                xxl: 2,
              }}
            />
          </Section>

          <Section title="Message Content">
            <div style={{ 
              border: '1px solid #d9d9d9', 
              borderRadius: '6px', 
              padding: '16px',
              backgroundColor: '#fafafa',
              minHeight: '200px'
            }}>
              <Typography.Text type="secondary" style={{ fontSize: '12px', display: 'block', marginBottom: '8px' }}>
                HTML Preview:
              </Typography.Text>
              <div 
                style={{ 
                  backgroundColor: 'white', 
                  padding: '16px', 
                  borderRadius: '4px',
                  border: '1px solid #e8e8e8'
                }}
                dangerouslySetInnerHTML={{ 
                  __html: data?.data?.body || '<p style="color: #999;">No content available</p>' 
                }}
              />
              
              <Typography.Text type="secondary" style={{ fontSize: '12px', display: 'block', marginTop: '16px', marginBottom: '8px' }}>
                Raw HTML Code:
              </Typography.Text>
              <div style={{ 
                backgroundColor: '#f5f5f5', 
                padding: '12px', 
                borderRadius: '4px',
                fontFamily: 'monospace',
                fontSize: '12px',
                whiteSpace: 'pre-wrap',
                wordBreak: 'break-all',
                maxHeight: '200px',
                overflow: 'auto',
                border: '1px solid #e8e8e8'
              }}>
                {data?.data?.body || 'No content available'}
              </div>
            </div>
          </Section>
        </Space>
      </Section>
    </Page>
  );
};

export default Component;
